.link-up,
.link-down,
.link-remove {
  opacity: 0.5;
  &:hover {
    opacity: 1;
  }
}
.link-send {
  svg {
    fill: #495057;
  }
  &:hover {
    svg {
      fill: #880E4F;
    }
  }
}

.link-remove {
  span {
    color: rgba(0.29, 0.31, 0.34, 0.54);
    font-size: 14px;
    display: inline-block;
    vertical-align: middle;
  }
}

.link-remove-black {
  opacity: 1;
  color: #495057;
  font-size: 14px;
  span, i {
    display: inline-block;
    vertical-align: middle;
  }
  i {
    position: relative;
    top: -2px;
    margin-right: 12px;
  }
}
.link-add-filial {
  color: #495057;
  font-size: 16px;
  line-height: 20px;
  position: relative;
  padding-left: 26px !important;
  &:before,
  &:after {
    content: "";
    display: block;
    position: absolute;
    background: #495057;
  }
  &:before {
    width: 1px;
    height: 12px;
    left: 6px;
    top: 50%;
    margin-top: -6px;
  }
  &:after {
    width: 12px;
    height: 1px;
    left: 0;
    top: 50%;
  }

  &:hover {
    text-decoration: none;
    color: #880e4f;
    &:before,
    &:after {
      background: #880e4f;
    }
  }
}