.dropdown-menu {
  box-shadow: 0px 24px 34px rgba(0, 0, 0, 0.16);
}
.dropdown-toggle::after {
  width: 10px;
  height: 5px;
  background-image: url('../img/icons/arrow.svg');
  border: none;
}
.chart-periode .dropdown-toggle {
  font-size: 16px;
}
.chart-periode .daterangepicker {
  border: none;
  color: #212529;
  background: url('../img/icons/arrow-data.svg') 100% 6px no-repeat;
  padding-right: 20px;
  &::placeholder {
    color: #212529;
  }
}
.dropdown-menu-remove {
  width: 280px;
  h5 {
    font-size: 16px;
    color: #495057;
  }
  p {
    white-space: normal;
    line-height: 20px;
    font-size: 14px;
    opacity: 0.54;
  }
  a {
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #880E4F;
  }
}

.drop-box {
  &.show {
    a.link-remove {
      opacity: 1;
    }
  }
}

.open-child-table {
  img {
    display: inline-block;
    position: relative;
    transform: rotate(180deg);
    opacity: 0.6;
  }
  &[aria-expanded="true"] {
    img {
      display: inline-block;
      position: relative;
      transform: rotate(0deg);
      opacity: 1;
    }
  }
}

.dropup {
  .dropdown-toggle {
    &::after {
      display: none;
    }
  }
  &.drop-info {
    p {
      font-size: 14px;
    }
    .name {
      display: inline-block;
      width: 60px;
      color: rgba(0.29,0.31,0.34,.54);
    }
  }
  .dropdown-menu {
    transform: translate3d(calc(-50% + 12px), calc(-100% - 10px), 0px) !important;
    &::after {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 10px 10px 0 10px;
      border-color: #ffffff transparent transparent transparent;
      position: absolute;
      bottom: -10px;
      left: 50%;
      margin-left: -10px;
    }
  }
}