.tooltip {
  opacity: 1;
  &-inner {
    background: #ffffff;
    color: #000000;
    box-shadow: 0 5px 10px rgba(0,0,0,0.1);
    padding: 18px 30px;
    opacity: 1;
  }
}
.bs-tooltip-top .arrow::before,
.bs-tooltip-auto[x-placement^="top"] .arrow::before {
  border-top-color: #ffffff;
}

.link-edit {
  svg {
    fill: #495057;
  }
  &:hover {
    svg {
      fill: #880E4F;
    }
  }
}