.page-item {
  .page-link {
    padding: 12px 0;
    width: 42px;
    height: 46px;
    text-align: center;
    color: #495057;
    border-color: #ced4da;
    &:focus {
      box-shadow: none;
    }
  }
  &.previous,
  &.next {
    .page-link {
      width: auto;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.page-item {
  &.active, &:hover {
    position: relative;
    z-index: 1;
    .page-link {
      background: linear-gradient(0deg, rgba(79, 8, 46, 0.08), rgba(79, 8, 46, 0.08)), #FFFFFF;
      border: 1px solid #4F082E;
      color: #4f082e;
    }
  }
  &.disabled {
    position: relative;
    z-index: 0;
    &.active, &:hover {
      .page-link {
        color: #adb5bd;
        pointer-events: none;
        cursor: auto;
        background: #fff;
        border-color: #dee2e6;
      }
    }
  }
}