.alert-success {
  width: 335px;
  background: #060606;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.16), 0px 8px 12px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  position: fixed;
  bottom: 30px;
  z-index: 99;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  p {
    margin-bottom: 0;
    color: #ffffff;
    font-size: 16px;
    letter-spacing: 0.25px;
  }
  button {
    &.close {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: 1.25px;
      text-transform: uppercase;
      color: #fe9fe6;
      text-shadow: none;
      opacity: 1;
    }
  }
}
