.modal-header .close {
  padding: 0.2rem 0.2rem;
  margin: -0.8rem -0.8rem -0.8rem auto;
}

#mapModal {
  .modal-header {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
  }
  .map {
    position: relative;
    .point {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
  .modal-footer {
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
  }
}