.nav .show>.nav-link,
.nav-link.active,
.nav-link:active,
.nav-link:hover,
.nav-link:focus,
.nav-pills .nav-link.active,
.navbar-nav .show>.nav-link {
  color: #fff;
  background-color: #880E4F;
}

.sidebar .logo,
.sidebar .nav-link.active,
.sidebar .nav-link:hover,
.sidebar .nav-sub ul a.active,
.sidebar .nav-sub ul a:hover {
  img {
    opacity: 1;
  }
}
.sidebar .nav-link,
.sidebar .nav-link,
.sidebar .nav-sub ul a,
.sidebar .nav-sub ul a {
  img {
    opacity: 0.54;
  }
}

.block-nav-tabs  {
  position: relative;
  &:after {
    content: "";
    display: block;
    border-bottom: 1px solid #DEE2E6;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: -1px;
  }
}
.nav-tabs .nav-link {
  color: #495057;
  font-size: 16px;
}
.nav-tabs .nav-link.active {
  color: #880E4F;
  box-shadow: 0 -2px 0 0 #880E4F inset;
}
.nav-tabs .nav-link:hover {
  color: #880E4F;
}