* {
  outline: none;
}

.text-green {
   color: #00c853;
}

.text-gray {
  color: rgba(0.29,0.31,0.34,0.54);
}

.price {
  font-family: 'PT Sans', serif;
  }
.logo {
  span:nth-child(2) {
    display: none;
  }
  img {
    width: 100%
  }
}

.sidebar-collapse {
  .logo {
    span:nth-child(1) {
      display: none;
    }
    span:nth-child(2) {
      display: block;
      width: 50px;
      text-align: center;
      img {
        max-width: 22px;
      }
    }
  }
}
.block__drag-and-drop {
  background-image: linear-gradient(to right, #DEE2E6 33%, rgba(255,255,255,0) 0%);
  background-position: top;
  background-size: 12px 2px;
  background-repeat: repeat-x;
  border-radius: 4px 4px 0 0;
  position: relative;
  &:before,
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    left: 0;
    top: 0;

  }
  &:before {
    background-image: linear-gradient(#DEE2E6 33%, rgba(255,255,255,0) 0%);
    background-position: left;
    background-size: 2px 12px;
    background-repeat: repeat-y;
    border-radius: 4px 0 0 4px;
  }
  &:after {
    background-image: linear-gradient(#DEE2E6 33%, rgba(255,255,255,0) 0%);
    background-position: right;
    background-size: 2px 12px;
    background-repeat: repeat-y;
    border-radius: 0 4px 4px 0;
  }
  &--head {
    background-image: linear-gradient(to right, #DEE2E6 33%, rgba(255,255,255,0) 0%);
    background-position: bottom;
    background-size: 12px 2px;
    background-repeat: repeat-x;
    span {
      font-size: 14px;
      opacity: 0.54;
    }
  }
  &--body {
    background-image: linear-gradient(to right, #DEE2E6 33%, rgba(255,255,255,0) 0%);
    background-position: bottom;
    background-size: 12px 2px;
    background-repeat: repeat-x;
    border-radius: 0 0 4px 4px;
    .block__left {
      width: 152px;
      height: 152px;
      position: relative;
      border: 1px solid rgba(222, 226, 230, 0.5);
      margin-right: 34px;
      border-radius: 4px;
      > img {
        width: 112px;
        height: 112px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -56px;
        margin-left: -56px;
        z-index: 0;
      }
      a.link-remove {
        position: absolute;
        bottom: 15px;
        right: 15px;
        z-index: 2;
      }
    }
    .block__right {
      width: calc(100% - 186px);
    }
    .drag-item {
      width: 100%;
      max-width: 152px;
      position: relative;
      border-radius: 4px;
      &.empty {
        background-color: #f8f9fa;
        min-height: 152px;
      }
      img {
        position: relative;
        z-index: 0;
        border-radius: 4px;
      }
      &.img-main {
        span {
          display: block;
          line-height: 16px;
          font-size: 14px;
          padding: 4px 8px;
          position: absolute;
          top: -10px;
          left: 50%;
          transform: translateX(-50%);
          z-index: 2;
          white-space: nowrap;
          background: #DEE2E6;
          border-radius: 5px;
        }
        &::before {
          border-radius: 4px;
          content: "";
          display: block;
          width: 100%;
          height: 100%;
          z-index: 1;
          position: absolute;
          border: 2px solid #DEE2E6;
        }
      }


      &-bottom {
        position: absolute;
        bottom: 10px;
        left: 0;
        width: 100%;
        z-index: 2;
      }
    }
  }
}
@media (min-width: 992px) {
  .main {
    margin-left: 256px
  }
}
@media (max-width: 768px) {
  div.dataTables_wrapper div.dataTables_paginate ul.pagination .previous,
  div.dataTables_wrapper div.dataTables_paginate ul.pagination .next {
    display: none;
  }
}