.sidebar {
  background: linear-gradient(0deg, #4F082E, #4F082E), linear-gradient(0deg, #880E4F, #880E4F), linear-gradient(0deg, #2B579A, #2B579A);
  &:hover {
    .logo {
      span:nth-child(1) {
        display: block;
      }
      span:nth-child(2) {
        display: none;
      }
    }
  }
}